<template>
  <div class="bg">
    <el-col class="main">
      <div class="map">
        <a href="">
          <img src="../../assets/images/map1-he.png" alt="">
        </a>
        <a class="taida" href="">
          <img src="../../assets/images/map1-bin.png" alt=""></a>
      </div>
      <el-row class="nav-wrapper">
        <ul class="nav">
          <el-menu
            :default-active="activeIndex2"
            class="el-menu-demo"
            mode="horizontal"
            @select="handleSelect"
            background-color="transparent"
            text-color="#fff"
            active-text-color="#9ac8f0">
            <el-menu-item class="el-menu-item" index="C / C++">C/C++</el-menu-item>
            <el-menu-item class="el-menu-item" index="Java">Java</el-menu-item>
            <el-menu-item class="el-menu-item" index="Python">Python</el-menu-item>
            <el-menu-item class="el-menu-item" index="设计">设计</el-menu-item>
            <el-menu-item class="el-menu-item" index="前端">前端</el-menu-item>
            <el-menu-item class="el-menu-item" index="嵌入式">嵌入式</el-menu-item>
          </el-menu>
        </ul>
      </el-row>
    </el-col>
  </div>
</template>
<script>

export default {
  data() {
    return {
      group: 'C / C++',
      url: '../barry.html',
      activeIndex: '1',
      activeIndex2: '1'
    };
  },

  methods: {
    handleSelect(key, keyPath) {
      console.log(key)
      switch (key) {
        case "Java":
          this.$router.push("/group/java");
          break;
        case "rust":
          this.$router.push("/group/rust");
          break;
        case "C / C++":
          this.$router.push("/group/c");
          break;
        case "Python":
          this.$router.push("/group/python");
          break;
        case "设计":
          this.$router.push("/group/design");
          break;
        case "前端":
          this.$router.push("/group/webfront");
          break;
        case "嵌入式":
          this.$router.push("/group/flushbonading");
          break;
      }
    },
  }
}

</script>

<style scoped>

.el-menu-demo {
  width: 1550px;
  display: flex;
  justify-content: space-around;
}

.el-menu-item {
  font-size: 24px;
}

* {
  margin: 0;
  padding: 0;
}

* {
  margin: 0;
  padding: 0;
}

.bg {
  height: 60vh;
  /*background-color: #FAFBFC;*/
}

.map {
  position: relative;
  z-index: 10;
  top: -8%;
  left: -40%;
}

.map a {
  display: inline-block;
  height: 140px;
  width: 100px;
}

.map .taida {
  width: 130px;
}

.map a img {
  width: 100%;
}

.main {
  width: 100%;
  height: 100%;
  background-image: url(../../assets/images/group-common.png);
  /*box-shadow: 2px 2px 2px 2px rgba(136, 136, 136, 0.52);*/
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* 主轴交叉轴居中对齐*/
}

.main ul.nav {
  /*background-color: rgb(228, 229, 229);*/
  list-style: none;
  position: relative;
  top: 13vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  border-radius: 5px;
}

</style>



